'use client';

import { useState } from 'react';

export default function UploadPage() {
    const [files, setFiles] = useState<File[]>([]);
    const [uploading, setUploading] = useState(false);
    const [results, setResults] = useState<any[]>([]);
    const [debugInfo, setDebugInfo] = useState<string>('');

    const handleFileSelect = (selectedFiles: FileList | null) => {
        if (selectedFiles) {
            setFiles(Array.from(selectedFiles));
        }
    };

    const uploadFiles = async () => {
        if (files.length === 0) return;

        setUploading(true);
        setResults([]);

        try {
            console.log('ðŸš€ Starting upload...');

            const formData = new FormData();
            files.forEach(file => {
                formData.append('files', file);
            });

            const response = await fetch('/api/upload', {
                method: 'POST',
                body: formData,
            });

            const result = await response.json();

            if (!response.ok) {
                throw new Error(result.error || `Upload failed with status ${response.status}`);
            }

            if (result.success) {
                setResults(result.data || []);
                console.log('âœ… Upload successful!', result.data);

                // AUTO-TRIGGER PARSING & MATCHING FOR EACH FILE
                for (const fileResult of result.data) {
                    if (fileResult.status === 'processing') {
                        await processCV(fileResult.id);
                    }
                }
            } else {
                throw new Error(result.error || 'Upload failed');
            }

        } catch (error) {
            console.error('âŒ Upload failed:', error);
            setResults([{
                originalName: 'Upload Failed',
                status: 'error',
                error: error instanceof Error ? error.message : 'Unknown error'
            }]);
        } finally {
            setUploading(false);
        }
    };

    // Process CV after upload
    const processCV = async (fileId: string) => {
        try {
            console.log(`ðŸ” Starting CV processing for: ${fileId}`);

            // Step 1: Parse CV
            const parseResponse = await fetch('/api/parse', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ fileId }),
            });

            const parseResult = await parseResponse.json();
            console.log('ðŸ“¨ Parse API response:', parseResult);

            if (!parseResponse.ok) {
                const errorDetails = parseResult.details || parseResult.error || 'Unknown parsing error';
                console.error('âŒ Parse API failed:', errorDetails);
                throw new Error(`Parsing failed: ${errorDetails}`);
            }

            console.log('âœ… CV parsed:', parseResult.candidate.name);

            // Step 2: Find matches
            const matchResponse = await fetch('/api/matching', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                },
                body: JSON.stringify({ candidateId: parseResult.candidate.id }),
            });

            const matchResult = await matchResponse.json();

            if (!matchResponse.ok) {
                throw new Error(matchResult.error || 'Matching failed');
            }

            console.log('âœ… Matches found:', matchResult.matches.length);

            // Update UI with processing results
            setResults(prev => prev.map(result =>
                result.id === fileId
                    ? {
                        ...result,
                        status: 'completed',
                        candidate: parseResult.candidate,
                        matches: matchResult.matches,
                        message: `âœ… Processed! Found ${matchResult.matches.length} matches`
                    }
                    : result
            ));

        } catch (error) {
            console.error('âŒ CV processing failed:', error);
            const errorMessage = error instanceof Error ? error.message : 'Unknown processing error';

            setResults(prev => prev.map(result =>
                result.id === fileId
                    ? {
                        ...result,
                        status: 'error',
                        error: `Processing failed: ${errorMessage}`
                    }
                    : result
            ));
        }
    };

    const clearFiles = () => {
        setFiles([]);
        setResults([]);
        setDebugInfo('');
    };

    return (
        <div className="min-h-screen bg-gray-50">
            {/* Hero Section */}
            <div className="bg-white border-b">
                <div className="container mx-auto px-6 py-12 max-w-5xl">
                    <div className="flex justify-between items-center">
                        <div>
                            <h1 className="text-4xl font-bold text-evernurse-dark mb-4">
                                Upload CVs
                            </h1>
                            <p className="text-lg text-gray-600 max-w-2xl">
                                Upload candidate CVs to automatically match them with open nursing positions.
                                Our AI engine analyzes skills, experience, and licenses to find the perfect fit.
                            </p>
                        </div>
                        <a
                            href="/results"
                            className="hidden md:flex bg-evernurse-teal text-white px-6 py-3 rounded-lg font-medium hover:bg-[#008f85] transition-all shadow-md hover:shadow-lg items-center gap-2"
                        >
                            <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5H7a2 2 0 00-2 2v12a2 2 0 002 2h10a2 2 0 002-2V7a2 2 0 00-2-2h-2M9 5a2 2 0 002 2h2a2 2 0 002-2M9 5a2 2 0 012-2h2a2 2 0 012 2m-3 7h3m-3 4h3m-6-4h.01M9 16h.01" />
                            </svg>
                            View Results Dashboard
                        </a>
                    </div>
                </div>
            </div>

            <div className="container mx-auto px-6 py-8 max-w-5xl">
                {/* File Upload Area */}
                <div className="bg-white rounded-xl shadow-sm border border-gray-200 p-8 mb-8">
                    <div className="border-2 border-dashed border-gray-300 rounded-lg p-12 text-center hover:border-evernurse-teal hover:bg-teal-50/30 transition-all cursor-pointer group">
                        <input
                            type="file"
                            multiple
                            accept=".pdf,.doc,.docx"
                            onChange={(e) => handleFileSelect(e.target.files)}
                            className="hidden"
                            id="file-upload"
                        />
                        <label htmlFor="file-upload" className="cursor-pointer block w-full h-full">
                            <div className="flex flex-col items-center">
                                <div className="w-20 h-20 bg-teal-50 rounded-full flex items-center justify-center mb-6 group-hover:scale-110 transition-transform">
                                    <svg className="w-10 h-10 text-evernurse-teal" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                                    </svg>
                                </div>
                                <p className="text-xl font-semibold text-evernurse-dark mb-2">
                                    Drop CV files here or click to browse
                                </p>
                                <p className="text-gray-500">
                                    Supports PDF, DOC, DOCX files â€¢ Max 10MB per file
                                </p>
                            </div>
                        </label>
                    </div>

                    {files.length > 0 && (
                        <div className="mt-8 animate-fade-in">
                            <div className="flex justify-between items-center mb-4">
                                <h3 className="font-semibold text-evernurse-dark">
                                    Selected Files ({files.length})
                                </h3>
                                <button
                                    onClick={clearFiles}
                                    className="text-sm text-red-600 hover:text-red-800 font-medium"
                                >
                                    Clear All
                                </button>
                            </div>
                            <div className="bg-gray-50 rounded-lg border border-gray-200 overflow-hidden">
                                <ul className="divide-y divide-gray-200 max-h-60 overflow-y-auto">
                                    {files.map((file, index) => (
                                        <li key={index} className="flex items-center justify-between p-4 hover:bg-white transition-colors">
                                            <div className="flex items-center">
                                                <svg className="w-5 h-5 mr-3 text-evernurse-teal" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
                                                </svg>
                                                <span className="font-medium text-gray-700">{file.name}</span>
                                            </div>
                                            <span className="text-xs font-medium text-gray-500 bg-gray-200 px-2 py-1 rounded">
                                                {(file.size / 1024 / 1024).toFixed(2)} MB
                                            </span>
                                        </li>
                                    ))}
                                </ul>
                            </div>

                            <div className="mt-6">
                                <button
                                    onClick={uploadFiles}
                                    disabled={uploading}
                                    className="w-full bg-evernurse-teal text-white px-6 py-4 rounded-lg font-bold text-lg hover:bg-[#008f85] transition-all shadow-md hover:shadow-lg disabled:opacity-50 disabled:cursor-not-allowed flex items-center justify-center gap-3"
                                >
                                    {uploading ? (
                                        <>
                                            <svg className="animate-spin h-6 w-6 text-white" fill="none" viewBox="0 0 24 24">
                                                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                                                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
                                            </svg>
                                            Processing {files.length} CVs...
                                        </>
                                    ) : (
                                        <>
                                            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 16v1a3 3 0 003 3h10a3 3 0 003-3v-1m-4-8l-4-4m0 0L8 8m4-4v12" />
                                            </svg>
                                            Upload and Match {files.length} CVs
                                        </>
                                    )}
                                </button>
                            </div>
                        </div>
                    )}
                </div>

                {/* Results Section */}
                {results.length > 0 && (
                    <div className="animate-fade-in-up">
                        <div className="flex justify-between items-center mb-6">
                            <h2 className="text-2xl font-bold text-evernurse-dark">Processing Results</h2>
                            {results.some(r => r.status === 'completed') && (
                                <a href="/results" className="text-evernurse-teal font-medium hover:underline flex items-center gap-1">
                                    View Full Dashboard
                                    <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 8l4 4m0 0l-4 4m4-4H3" />
                                    </svg>
                                </a>
                            )}
                        </div>

                        <div className="space-y-4">
                            {results.map((result, index) => (
                                <div key={index} className={`bg-white rounded-lg border p-6 shadow-sm transition-all ${result.status === 'error' ? 'border-red-200 border-l-4 border-l-red-500' :
                                        result.status === 'completed' ? 'border-gray-200 border-l-4 border-l-evernurse-teal' :
                                            'border-blue-200 border-l-4 border-l-blue-500'
                                    }`}>
                                    <div className="flex items-start justify-between">
                                        <div>
                                            <div className="flex items-center gap-3 mb-2">
                                                {result.status === 'completed' ? (
                                                    <span className="bg-teal-100 text-evernurse-teal p-1 rounded-full">
                                                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                                                        </svg>
                                                    </span>
                                                ) : result.status === 'error' ? (
                                                    <span className="bg-red-100 text-red-600 p-1 rounded-full">
                                                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                                                        </svg>
                                                    </span>
                                                ) : (
                                                    <span className="bg-blue-100 text-blue-600 p-1 rounded-full animate-pulse">
                                                        <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                                            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                                                        </svg>
                                                    </span>
                                                )}
                                                <h4 className="font-bold text-lg text-gray-900">{result.originalName}</h4>
                                            </div>

                                            {result.candidate && (
                                                <div className="ml-8 text-gray-600">
                                                    <p className="font-medium text-evernurse-dark">{result.candidate.name}</p>
                                                    <p className="text-sm mt-1">
                                                        {result.candidate.experience} years exp â€¢ {result.candidate.licenses?.join(', ') || 'No licenses'}
                                                    </p>
                                                </div>
                                            )}
                                        </div>

                                        {result.matches && result.matches.length > 0 && (
                                            <div className="text-right">
                                                <span className="inline-block bg-teal-50 text-evernurse-teal px-3 py-1 rounded-full text-sm font-bold border border-teal-100">
                                                    {result.matches.length} Job Matches
                                                </span>
                                            </div>
                                        )}
                                    </div>

                                    {result.matches && result.matches.length > 0 && (
                                        <div className="mt-4 ml-8 pt-4 border-t border-gray-100">
                                            <p className="text-xs font-semibold text-gray-500 uppercase tracking-wider mb-2">Top Match</p>
                                            <div className="flex justify-between items-center bg-gray-50 p-3 rounded">
                                                <div>
                                                    <p className="font-medium text-evernurse-dark">{result.matches[0].jobTitle}</p>
                                                    <p className="text-xs text-gray-500">{result.matches[0].location}</p>
                                                </div>
                                                <div className="text-right">
                                                    <span className="text-lg font-bold text-evernurse-teal">{result.matches[0].score}%</span>
                                                </div>
                                            </div>
                                        </div>
                                    )}
                                </div>
                            ))}
                        </div>
                    </div>
                )}

                {/* Debug Info Toggle */}
                <div className="mt-12 border-t pt-6">
                    <details className="text-sm text-gray-500">
                        <summary className="cursor-pointer hover:text-evernurse-teal transition-colors">Show Debug Information</summary>
                        <div className="mt-4 p-4 bg-gray-100 rounded-lg overflow-auto max-h-60 font-mono text-xs">
                            <button onClick={() => setDebugInfo('')} className="mb-2 text-blue-600 hover:underline">Clear</button>
                            <pre>{debugInfo || 'No debug info available'}</pre>
                        </div>
                    </details>
                </div>
            </div>
        </div>
    );
}